body {
    margin: 0;
    font-family: Georgia, "Times New Roman", Times, serif;
    color: azure;
    background-color: darkslateblue;
}
#root {
    display: flex;
    flex-direction: column;
}
code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
        monospace;
}
header {
    padding: 1rem;
    background-color: lightsalmon;
    text-align: center;
}
.container .col {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}
.card {
    justify-content: center;
    width: 18rem;
    position: relative;
    background-color: transparent;
    margin: 10px;
    border-color: aliceblue;
    width: 250px;
    overflow: hidden;
}
.btn {
    background-color: black;
    border-color: transparent;
}
.btn:hover {
    background-color: lightsalmon;
    border-color: transparent;
}
a {
    text-decoration: none;
    color: lightcyan;
}
.container-button {
    align-items: center;
    justify-content: center;
}
.row-desc {
    display: flex;
}
.info-container {
    padding: 50px;
}
.col {
    padding: 20px;
}

.container {
    text-align: center;
    justify-content: center;
    align-items: center;
}
.heading {
    color: lightsalmon;
    display: inline;
}
